.absolute-pos {
  position: absolute;
  top: 250px;
  left: 450px;
  display: block;
  width: 380px;
  div {
    position: relative;
    z-index: 1;
  }
  div:nth-child(1){
    position: relative;
    bottom: 50px;
  }
  img:hover {
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;
  }
  img:active {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
  }
}

.createImgContainer {
  width: 100%;
  height: 605px;
  background-size: contain;
  background-repeat: no-repeat;
  padding-left: 2rem;
  position: relative;
  overflow: visible;
}

.react-resizable {
  position: relative;
}
.react-resizable-handle {
  position: absolute;
  width: 55px;
  height: 20px;
  border-radius: 5px;
  opacity: 1;
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
  box-sizing: border-box;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3Csvg viewBox='0 0 182.931 182.931' fill='%23000000' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M173.93 92.798c-4.971 0-9 4.029-9 9v50.404L30.728 18h50.404c4.971 0 9-4.029 9-9s-4.029-9-9-9H9C4.03 0 0 4.029 0 9 v72.132c0 4.971 4.029 9 9 9s9-4.029 9-9V30.729l134.202 134.202h-50.404c-4.971 0-9 4.029-9 9s4.029 9 9 9h72.132 c4.971 0 9-4.029 9-9v-72.132C182.93 96.828 178.901 92.798 173.93 92.798z' style='fill: rgb(255  255  255)%3B'/%3E%3C/svg%3E");
  background-position: bottom right;
  padding: 0 3px 3px 0;
  &::after {
    content: "move me!";
    font-size: 0.75rem;
    color: white;
    position: relative;
    top: 20px;
    width: 100px;
    text-align: right;
    left: 20px;
    transition: all 2s ease-in-out;
  }

  &:hover {
    &::after {
      opacity: 0;

    }
  }
}
.react-resizable-handle-sw {
  bottom: -20px;
  left: -20px;
  cursor: sw-resize;
  transform: rotate(90deg);
}
.react-resizable-handle-se {
  bottom: -20px;
  right: -10px;
  cursor: se-resize;
}
.react-resizable-handle-nw {
  top: 0;
  left: 0;
  cursor: nw-resize;
  transform: rotate(180deg);
}
.react-resizable-handle-ne {
  top: 0;
  right: 0;
  cursor: ne-resize;
  transform: rotate(270deg);
}
.react-resizable-handle-w,
.react-resizable-handle-e {
  top: 50%;
  margin-top: -10px;
  cursor: ew-resize;
}
.react-resizable-handle-w {
  left: 0;
  transform: rotate(135deg);
}
.react-resizable-handle-e {
  right: 0;
  transform: rotate(315deg);
}
.react-resizable-handle-n,
.react-resizable-handle-s {
  left: 50%;
  margin-left: -10px;
  cursor: ns-resize;
}
.react-resizable-handle-n {
  top: 0;
  transform: rotate(225deg);
}
.react-resizable-handle-s {
  bottom: 0;
  transform: rotate(45deg);
}
